<template>
  <div class="box">
    <el-row>
      <el-col :span="24">
        <el-card class="box_card">
          <div slot="header">今日打卡人数统计</div>
          <chart :options="optionsA" ref="A" style="width: 100%;"></chart>
          <div id="myChart"></div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import audio from './audio'
export default {
  components: {
    maudio: audio
  },
  mounted () {
    const chartA = this.$refs['A'];
    chartA.showLoading();
    this.getRequest('/summary').then(res => {
      let array = res.data.data;
      // eslint-disable-next-line camelcase
      let legendTitle = [];
      // array.forEach(element => {
      //   legendTitle.push(element.name);
      // });
      console.log(typeof (array));

      for (var i = 0; i < array.length; i++) {
        legendTitle.push(array[i].name);
      }
      console.log(legendTitle);

      chartA.mergeOptions({
        legend: { data: legendTitle },
        series: { data: array }
      });
      setTimeout(() => {
        chartA.hideLoading();
      }, 1000)
    }).catch(e => {
      chartA.hideLoading();
      console.log('错误是' + e);
    })


    // this.axios.get('getPieData')
    //   .then(data => {
    //     console.log(data);
    //     if (data.errno === 0) {
    //       const options = data.data;
    //       chartA.mergeOptions({
    //         series: options.optionsA.series
    //       });
    //     }
    //     setTimeout(() => {
    //       chartA.hideLoading();
    //     }, 1000);
    //   }).catch(error => {
    //     chartA.hideLoading();
    //     console.log(error);
    //   });
  },
  data () {
    return {
      optionsA: {
        title: {
          show: true,
          text: '今日打卡人数统计',
          subtext: '纯属虚构',
          x: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        legend: {
          type: 'scroll',
          orient: 'vertical',
          x: 'left',
          data: ['直接访问', '邮件营销', '联盟广告', '视频广告', '搜索引擎']
        },
        // toolbox: {
        //   show: true,
        //   feature: {
        //     mark: { show: true },
        //     dataView: { show: true, readOnly: false },
        //     magicType: {
        //       show: true,
        //       type: ['pie', 'funnel'],
        //       option: {
        //         funnel: {
        //           x: '25%',
        //           width: '50%',
        //           funnelAlign: 'left',
        //           max: 1548
        //         }
        //       }
        //     },
        //     restore: { show: true },
        //     saveAsImage: { show: true }
        //   }
        // },
        calculable: true,
        series: [
          {
            name: '访问来源',
            type: 'pie',
            radius: '65%',
            center: ['50%', '60%'],
            data: [
              {
                value: null,
                name: '直接访问',
                itemStyle: {
                  normal: {
                    color: '#2EC7C9'
                  }
                }
              },
              {
                value: null,
                name: '邮件营销',
                itemStyle: {
                  normal: {
                    color: '#B6A2DE'
                  }
                }
              },
              {
                value: null,
                name: '联盟广告',
                itemStyle: {
                  normal: {
                    color: '#5AB1EF'
                  }
                }
              },
              {
                value: null,
                name: '视频广告',
                itemStyle: {
                  normal: {
                    color: '#FFB980'
                  }
                }
              },
              {
                value: null,
                name: '搜索引擎',
                itemStyle: {
                  normal: {
                    color: '#D87A80'
                  }
                }
              }
            ]
          }
        ]
      }
    }
  },
  created () {

  },
  methods: {

  }
}

</script>

<style >
/* .box {
  width: 60px;
  height: 25px;
  cursor: pointer;
} */
</style>


